.card {
    background-color: #262627;
    border: none;
    text-align: left;
    padding: 10px;
    color: #fff;
    margin-bottom: 10px;
    border-radius: 5px;
}

.card-title {
    display: flex;
    justify-content: space-between;
}

.card-title p {
    margin: 0px;
    font-size: 15px;
}

.row_flex{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.card-picture{
    display: flex;
    align-items: center;
}