/* TwoColumnLayout.css */

.two-column-layout {
  display: flex;
  align-items: flex-start;
  padding: 20px;
  margin-right: 20px;
  margin-left: 20px;
  border-radius: 5px;
}

.proposal-left-column,
.proposal-left-column:hover {
  flex: 1;

  background-color: #fff;
  padding: 20px;
  border-radius: 15px;
}

.proposal-right-column,
.proposal-right-column:hover {
  flex: 1;
  max-width: 25%;
  margin-left: 20px;
  padding-right: 20px;
  background-color: #fff;
  padding: 20px;
  border-radius: 15px;
}

