/* Dashboard Page Styles */
.dashboard-container {
  display: flex;
  flex-direction: row;
  background-color: #3A3A3A;
  height: 100%;
  width: 100%;
  min-height: 100vh;
  background-image: url('../../../../src/assets/background.png');
  background-size: contain;
  background-position: center;
}

.main-content {
  display: flex;
  flex-direction: column;
  width: calc(100% - 380px);
  flex-grow: 1;
}

.halfWidth {
  width: calc(100% - 380px);
}

.fullWidth {
  width: calc(100% - 120px);
}

.menuText {
  color: #fff;
  text-decoration-line: none;
  cursor: pointer
}

.content {
  flex: 1;
  display: flex;
  padding: 20px;
  padding-left: 80px;
}

.horizontal-rule {
  border: 1px solid #F0F0F0;
  margin: '20px 0'

}

/* Basic styling for the container */
.activity-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 20px;
  margin-top: 0% !important;
  border: none;
}

/* Left column styles */
.user-activity {
  /* flex: 1; */
  padding-right: 20px;
}

/* Right column styles */
.app-activity {
  /* flex: 1.5; */
  padding-left: 20px;
}

.app-card-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
}

/* Title styles */
.app-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 10px;
  color: #fff;
}

/* Chart image styles */
.chart {
  max-width: 100%;
  height: auto;
  margin-top: 20px;
}

/* Card styles */
.app-card {
  background-color: #262627;
  border: none;
  padding: 10px;
  height: 60px;
  width: 100%;
  color: #fff;
  border-radius: 5px;
}

.app-card-title {
  display: flex;
  justify-content: space-between;
}

.app-card-title p {
  margin: 0px;
  font-size: 15px;
  margin-bottom: 10px;
}

.selectedDate {
  background-color: transparent !important;
  border: none !important;
  color: #fff !important;
  }
  .selectedDate > option {
  background-color: #262627;
  
}